import Button from "./Button"

const Header = ({title, onOpen, open}) => {
  return ( 
    <header className = 'header'>
      <h1>{title}</h1>
      <Button 
      onClick = {onOpen} 
      color = {open ? 'red' : 'green'} 
      text = {open ? 'Close' : 'Add'} 
      />
    </header>
   );
}

export default Header;